
import axios from "axios"

let api = 'https://89.251.147.220:8443/'

export default class ProductsService {
    
    static async getAll(limit: number = 10, page: number){
        const response = await axios.get(api + `products?limit=${limit}&page=${page}`)
        return response
    }

    static async getById(id: string | undefined){
        const response = await axios.get(api + `products?id=` + id)
        return response.data
    }

    static async getFavourites(favourites: number[]){
        const response: any = await fetch(api + 'favourites/get', {
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({favourites}),
            method: 'POST'
        }).then(response => response.json())
        return response
    }

    static async getImageById(id: string | undefined){
        const response = await axios.get(api + 'products?id=' + id)
        return response.data[0].url
    }

    static async getLap(){
        const response = await axios.get(api + 'products/top/Laptops/RAM')
        return response.data
    }

    static async filterProducts(filters: {[propName: string]: string | undefined | string[][]}){
        const response = await fetch(api + 'products/search/withFilter', {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(filters)
        }).then(response => response.json())
        return response
    }

    static async sessionAddFavourite(id: string){
        const response = await axios.get(api + `Session/addFavourite?id=${id}`)
        return response
    }
}