
import { useDispatch, useSelector } from "react-redux"
import favourite from "../helpers/addFavourite"
import Store from "../types/Store"
import { Dispatch } from "redux"

export function RemoveFav({id}: {id: number}) {
    
    const isAuth = useSelector((state: Store) => state.isAuth.auth)
    const dispatch: Dispatch = useDispatch()


    const RFEvent = () => {
        if(isAuth){
            favourite(id, dispatch)
        } else {
            let list = sessionStorage.getItem('favouritesList')
            if(list) {
                let favourites: number[] = JSON.parse(list).list
                console.log(favourites)
                let index = favourites.indexOf(id)
                console.log(favourites, index)
                favourites.splice(index, 1)
                sessionStorage.setItem('favouritesList', JSON.stringify({list: favourites}))
                dispatch({type: 'addFavourite', payload: {list: favourites}})
            }
        }
    }

    return (
        <div className="remove-from-favourites" onClick={RFEvent}>
            <img src="/images/remove-from-favourite.png" alt="#" title='   .' />
        </div>
    )
}
