import { useSelector } from "react-redux"
import Store from "../../../../types/Store"
import { Link } from "react-router-dom"
import { useEffect } from "react"

interface SearchListI {
    search: string,
}

export function SearchList({search}: SearchListI) {

    let titlesList = useSelector((state: Store) => state.titles.list)
    let titles: Array<string | undefined> = titlesList.map((title) => {
        let result = search.split(' ').reduce((total: number, partOfTitle: string) => {
            if(partOfTitle && title.includes(partOfTitle)){
                total += 1
            }
            return total += 0
        }, 0)
        if(result <= title.split(' ').length && result === search.split(' ').filter(i => i !== '').length) {
            return title
        }
    })
    titles = titles.filter(title => title !== undefined)
    useEffect(() => {
        document.addEventListener('keyup', (event: KeyboardEvent) => {
            if(event.key === 'Enter' && titles[0]){
                window.location.href = window.location.origin + `/search/${titles[0].split('/')[0]}`
            }
        })       
    }, [titles])

    return (
        <>
            {titles.length > 0 && <ul className='search-list'>
                {titles.map((title) => {
                    return <li><Link to={`/search/${title}`} >{title}</Link></li>
                })}
            </ul>}
        </>
    )
}