import { Main } from '../pages/Main/Main'
import Cabinet from "../pages/Cabinet";
import Error from "../pages/Error";
import ProductIdPage from "../pages/ProductIdPage";
import Favourites from "../pages/Favourites/Favourites";
import Compare from "../pages/Compare/Compare";
import { ReactNode } from "react";
import { SearchPage } from "../pages/SearchPage/SearchPage";

interface RoutesI {
    path: string,
    element: ReactNode,
    exact?: boolean,
    key?: string
}

export const privateRoutes: RoutesI[] = [
    {path: '/', element: <Main />, exact: true, key: 'keyMain'},
    {path: '/Main', element: <Main />, exact: true, key: 'keyMain'},
    {path: '/products/:id', element: <ProductIdPage />, exact: true, key: 'keyProductsID'},
    {path: '/favorites', element: <Favourites />, exact: true, key: 'keyFavorites'},
    {path: '/cabinet', element: <Cabinet />, exact: true, key: 'keyCabinet'},
    {path: '*', element: <Error />, exact: true, key: 'keyError'},
    {path: '/compare', element: <Compare />, exact: true, key: 'keyCompare'},
    {path: '/search/:title', element: <SearchPage /> , exact: true, key: 'keySearchPage'},
]

export const publicRoutes: RoutesI[] = [
    {path: '*', element: <Error />, exact: true, key: 'keyError'},
    {path: '/favorites', element: <Favourites />, exact: true, key: 'keyFavorites'},
    {path: '/', element: <Main />, exact: true, key: 'keyProducts'},
    {path: '/Main', element: <Main />, exact: true, key: 'keyProducts'},
    {path: '/compare', element: <Compare />, exact: true, key: 'keyCompare'},
    {path: '/products/:id', element: <ProductIdPage />, exact: true, key: 'keyProductsID'},
    {path: '/search/:title', element: <SearchPage /> , exact: true, key: 'keySearchPage'},
]