import { BaseSyntheticEvent, ChangeEvent } from "react"
import ProductsService from "../../../API/API"
import { useParams } from "react-router-dom"
import { Accordion } from "../ui/Accordion"



export function Filter({setSearchProducts, filters, type}: {setSearchProducts: Function, filters: any, type: string}) {
    
    const keys: string[] = Object.keys(filters)
    let title: string | undefined = useParams().title
    const click = async (event: BaseSyntheticEvent<SubmitEvent>) => {

        event.preventDefault()

        let nodesList: HTMLInputElement[][] = keys.map((key: string) => Array.from(event.target.elements[key]))
        let formData: string[][] = [];
        nodesList.forEach((nodes: HTMLInputElement[]) => {
            nodes.forEach((node: HTMLInputElement) => {
                if(node.checked == true){
                    formData.push([node.name, node.value])
                }
            })
        })
        const types = ['Laptops', 'Phones']
        let data: any;
        if(title) {
            if (types.includes(title)){
                data = await ProductsService.filterProducts({
                    search: '',
                    type: type,
                    filters: formData
                })
            } else {
                data = await ProductsService.filterProducts({
                    search: title,
                    type: type,
                    filters: formData
                })
            }
        }
        setSearchProducts(data.products)
    }

    return (
        <form action="#" className="filter-form" onSubmit={click} >
            Filters
            <div className="filters__list">
                {keys.map((title: string) => {
                    return  <div className="filter" key={title}>
                                <Accordion
                                    title={title}
                                    titleClass={"filter-title"}
                                >
                                    {filters[title].map((value: string) =>
                                        <p>{value}
                                            <input key={title + value}
                                                className="filter-input"
                                                type='checkbox'
                                                name={title}
                                                value={value}
                                            />
                                        </p>
                                    )}
                                </Accordion>

                            </div>
                })}
            </div>
            <button type="submit" className="submit__filters" > </button>
        </form>
    )
}