
import { FormEvent, useState } from "react";
import { useDispatch } from "react-redux";
import UserDataType from "../../../types/userData";
import { Dispatch } from "redux";

interface myFormPropsI {
    method: Function,
    title: string,
    recovery?: boolean
}

interface formData {
    [propName: string | number]: string
}

interface isTrueI {
    message: string,
    userData: {
        login: string,
        favourites: number[]
    }
}

function MyForm({method, title, recovery}: myFormPropsI){

    const [name, setName] = useState<string>('')
    const [nameDirty, setNameDirty] = useState<boolean>(false)
    const [password, setPassword] = useState<string>('')
    const [oldPassword, setOldPassword] = useState<string>('')
    const [isRepeatPass, setIsRepeatPass] = useState<boolean>(false)
    const dispatch: Dispatch = useDispatch()

    function ValidateForm(data: formData){

        let re = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
        
        for(let i in data){
            if(data[i].length < 1){
                console.log('')
                return false
            }

            if(!re.test(String(data.login).toLowerCase())){
                setNameDirty(true)
                return false
            }

            if(data.oldPassword === data.pass){
                setIsRepeatPass(true)
                return false
            }

        }
        return data
    }

    interface FormElements extends HTMLFormControlsCollection{
        login: HTMLInputElement;
        password: HTMLInputElement;
        oldPassword: HTMLInputElement;
    }

    interface CustomForm extends HTMLFormElement {
        readonly elements: FormElements
    }

    async function submitForm(event: FormEvent<CustomForm>){
        event.preventDefault()
        let allData: UserDataType = {}
        const elements = event.currentTarget.elements
        allData.login = elements.login.value

        allData.pass = elements.password.value
        if(elements.oldPassword){
            allData.oldPass = elements.oldPassword.value
        }

        if(ValidateForm(allData)){
            const isTrue: isTrueI = await method(allData);
            if(isTrue){
                dispatch({type: 'AUTH_TRUE', payload: {auth: true, userData: isTrue.userData}})
                dispatch({type: 'addFavourite', payload: {list: isTrue.userData.favourites}})
                if(recovery){
                    window.location.href = 'http://localhost:8080/cabinet'
                }
            }
        }
    }

    return(
        <div className="my__form">
            <div className="my__form-title">
                <h3>{title}</h3>
                <p>let's start</p>
            </div>
            <form className="my__form-form" onSubmit={submitForm} action="multipart/form-data" method="post">
                <label>
                    {nameDirty && ' '}
                    <p>Name</p>
                    <input name="login" type="text" value={name} onChange={(event)=>{
                        event.preventDefault()
                        setName(event.target.value)
                    }} />
                </label>
                {recovery && <label><p>old Password</p><input name="oldPassword" type="text" value={oldPassword} onChange={(event)=>{
                        setOldPassword(event.target.value)
                     }} />
                </label>}
                <label>
                    <p>Password</p>
                    <input name="password" type="text" value={password} onChange={(event)=>{
                        setPassword(event.target.value)
                    }} />
                </label>
                {isRepeatPass && <p style={{color: '#934'}}>   !!</p> }
                <button className="my__form-submit" type="submit">{title}</button>
            </form>
        </div>
    )
}

export default MyForm;