import { useEffect, useState } from "react"
import { useParams, Params, Link } from "react-router-dom"
import useProducts from "../../components/hooks/useProducts"
import ProductsService from "../../API/API"
import IProduct from "../../interfaces/IProduct"
import { Filter } from "./helpers/Filter"
import { ProductImage } from "../../shared/ProductImage"
import Loader from "../../components/UI/Loader"

export function SearchPage() {

    const params: Readonly<Params<string>> = useParams()
    const [searchProducts, setSearchProducts] = useState<IProduct[]>([])
    const [filters, setFilters] = useState({})
    const types: string[] = ["Laptops", "Phones"]
    const [fetchSP, isLoad] = useProducts(async () => {
        let response;
        if(params.title){
            if (types.includes(params.title)){
                response = await ProductsService.filterProducts({search: '', type: params.title, filters: []})
            } else {
                response = await ProductsService.filterProducts({search: params.title, type: 'none', filters: []})
            }    
        }
        setSearchProducts(response.products)
        const filters = response.filters;
        let result: any = {};
        const filtersKeys = Object.keys(response.filters)
        filtersKeys.forEach(filter => {
            let filterKeys = Object.keys(filters[filter])
            result[filter] = filterKeys
        })
        setFilters(result)
    })

    useEffect(() => {
        fetchSP()
    }, [params.title])

    return (
        <>
            <h1>     {params.title}</h1>
            <div className="search__container">
                <Filter type={searchProducts.length ? searchProducts[0].type : ''} filters={filters} setSearchProducts={setSearchProducts} />
                <div className="search__content">
                    {searchProducts.length > 0 ? searchProducts.map(product =>
                        <div className="search__product">
                            <div className="search__product-img">
                                <Link to={`/products/${product._id}`}>
                                    <ProductImage image={product.img[0]} />
                                </Link>
                            </div>
                            <div className="search__product-title">
                                <Link to={`/products/${product._id}`}>
                                    <h1>{product.title.split(' ').splice(1, 5).join(' ')}</h1>
                                </Link>
                            </div>
                        </div>
                    ) : <Loader /> }
                </div>
            </div>
        </>
    )
}