
import { AxiosError } from 'axios'
import {useState} from 'react'

function useProducts(callback: Function){

    
    const [load, setLoad] = useState(false)
    const [error, setError] = useState<string>('')

    const fetch = async () => {
        try {
            setLoad(true)
            await callback()
        } catch (e: any) {
            setError(e.message)
        } finally {
            setLoad(true)
        }
    }

    return [fetch, load, error] as const
}

export default useProducts;
