
import axios from "axios";

let api = 'https://89.251.147.220:8443/'

export default class UserService {

    static async reg(data: {login: string, pass: string}){
        let responseData = await fetch(api + 'reg', {
            credentials: 'include',
            method: 'POST',
            body: JSON.stringify(data)
        }).then(response => response.json())
        return responseData
    }

    static async log(data: {login: string, pass: string}){
        let responseData = await fetch(api + 'log', {
            credentials: 'include',
            method: 'POST',
            body: JSON.stringify(data)
        }).then(response => {
            return response.json()
        })
        return responseData
    }

    static async getCok(){
        const response = await axios.get(api + `checkCookie`, {
            withCredentials: true
        })
    }

    static async postRecoveryData(data: BodyInit){
        const response = await fetch(api + 'rec', {
            method: 'POST',
            body: JSON.stringify(data),
            credentials: 'include'
        }).then(response => response.json())
        return response
    }

    static async editFavourite(id: number){
        const response = await axios.get(api + `editFavourite?id=${id}`, {
            withCredentials: true
        })
        return response.data
    }

    static async getFavourites(){
        const response = await axios.get(api + `editFavourite?id=`, {
            withCredentials: true
        })
        return response.data
    }

    static async autorization(){
        const response: any = await fetch(api + 'checkCookie', {
            method: 'GET',
            credentials: 'include'
        }).then(response => response.json())
        if(response.userData.login !== undefined){
            return response
        }
        return false
    }

    static async logOut() {
        await fetch(api + 'deleteCookie', {
            method: 'GET',
            credentials: 'include'
        })
        window.location.href = '/'
    }
}