import { BaseSyntheticEvent } from "react"
import { Dispatch } from "redux"


function addToNav(event: BaseSyntheticEvent<MouseEvent>, crumbsList: string[], dispatch: Dispatch){

    let link = event.target.closest('a').getAttribute('href')
    let isIncludes = crumbsList.includes(link)
    if(!isIncludes && link !== '/') {
      crumbsList.push(link)
      dispatch({type: 'new', payload: crumbsList})
    } else if (isIncludes && link !== '/'){
      let index = crumbsList.indexOf(link)
      crumbsList.splice(index, 1)
      crumbsList.push(link)
      dispatch({type: 'new', payload: crumbsList})
    }

    return crumbsList
}

export default addToNav;