import IProduct from "../interfaces/IProduct";

interface defaultStateI {
    products: IProduct[]
}

interface actionI {
    type: string,
    products: IProduct[]
}

const defaultState = {
    products: []
}

const search = (state = defaultState, action: actionI) => {
    switch (action.type) {
        case 'search':
            return {type: action.type, products: {...state, ...action.products}}
    
        default:
            return {...defaultState}
    }
}

export default search