
import {useState} from 'react'
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import addToNav from '../addToNav';
import Store from '../../../types/Store';


function Catalog({adaptive}: {adaptive: boolean}) {

    const [menu, setMenu] = useState<Boolean>(false)
    const isAuth = useSelector((state: Store) => state.isAuth.auth)
    const crumbsList = useSelector((state: Store) => state.crumbs.list)
    const dispatch = useDispatch()
    document.body.addEventListener('click', (event: any) => {
      if(!event.target.closest('.catalog__menu') && !event.target.closest('.catalog__button')){
        (document.querySelector('.catalog__button') as HTMLDivElement).classList.remove('active-catalog')
        setMenu(false)
      }
    })

    const btnClick = () => {
      setMenu(menu => !menu);
      (document.querySelector('.catalog__button') as HTMLDivElement).classList.toggle('active-catalog')
    }

    return (
        <div className="catalog">
          <div className="catalog__button" onClick={btnClick}>
            <img src="/images/white-bag.png" alt="#" />
            {!adaptive && <span></span>}
          </div>
          {menu && 
            <nav className='catalog__menu'>
              <Link to={isAuth ? '/cabinet' : '/'} onClick={event => addToNav(event, crumbsList, dispatch)}>About us</Link>
              <Link to='/' onClick={event => addToNav(event, crumbsList, dispatch)}>Main</Link>
              <Link to='/favorites' onClick={event => addToNav(event, crumbsList, dispatch)}>Favorites</Link>
              <Link to='/compare' onClick={event => addToNav(event, crumbsList, dispatch)}>Compare</Link>
            </nav>
          }
        </div>
    )
}

export default Catalog;
