import { BaseSyntheticEvent, MouseEventHandler, ReactNode, useState } from "react"

interface AccordionI {
    title: string,
    children: any,
    titleClass: string,
}


export function Accordion({title, children, titleClass}: AccordionI) {
    
    const [accordion, setAccordion] = useState<boolean>(false)
    const accordionClass: string = accordion ? 'accordion-btn active-accordion ' : 'accordion-btn '
    const accordionEvent: MouseEventHandler = (event: BaseSyntheticEvent) => {
        console.log(event.target.parentNode.lastChild.classList.toggle('active'))
        setAccordion((prev: boolean) => !prev)
    }

    return (
        <>
            <h3 className={accordionClass + titleClass} onClick={accordionEvent}>{title}</h3>
            <p className={'accordion'}>
                {children.map((child: ReactNode) => {
                    return child
                })}
            </p>
        </>
    )
}