import axios from "axios"

const phonesKeys = [
    'ANTUTU9', 'BATTERY', 'CPU',
    'DISPLAY', 'DPI', 'GEEKBENCH_MC',
    'GEEKBENCH_SC', 'GPU', 'INTERNAL_STORAGE',
    'SP', 'TECHNOLOGIES'
]
const laptopsKeys = ['CacheMemory', 'Chipset', 'CPU', 'CPUFrequency', 'CPUFrequencyTurbo',
                    'CPUSeries', 'DisplayDiagonal', 'DisplayResolution', 'Frequency', 'GPU', 'NumberOfCPUCores',
                    'RAM', 'ScreenMatrixType', 'SoundSystem', 'SSDInterface', 'SSDSolidStateDriveCapacity',
                    'TypeOfDataCarrier', 'TypeOfGPU', 'TypeOfRAM', 'Webcam', 'Wi-Fi']
                    
const api = 'https://89.251.147.220:8443/'

export default class compareAPI {

    static async getPhonesData(){
        let phonesData: {[propName: string]: object} = {}
        for(let key of phonesKeys){
            let response = await axios.get(api + 'products/top/Phones/' + key)
            phonesData[key] = response.data     
        }
        return phonesData
    }

    static async getLaptopsData(){
        let laptopsData: {[propName: string]: object} = {}
        for(let key of laptopsKeys){
            let response = await axios.get(api + 'products/top/Laptops/' + key)
            laptopsData[key] = response.data  
        }
        return laptopsData
    }
}