

type crumbsT = {
  type: string,
  payload: Array<string>
}

type defaultType = {
  list: Array<string>
}

const defaultState: defaultType = {
    list: []
}


export const crumbs = (state = defaultState, action: crumbsT) => {
    switch(action.type){
      case 'new':
        return {...state, list: action.payload}

      case 'AUTH_FALSE':
        return {...state, list: action.payload}

      default: 
        return state
    }
}