import {MouseEvent} from 'react'
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Store from '../../types/Store';
import { Dispatch } from 'redux';

function Crumbs() {

    const crumbsList: string[] = useSelector((state: Store) => state.crumbs.list)
    const isAuth: boolean = useSelector((state: Store) => state.isAuth.auth)
    const dispatch: Dispatch = useDispatch()
    const replace = (event: MouseEvent<HTMLAnchorElement>, link: string) => {
        if(isAuth){
            let index = crumbsList.indexOf(link)
            if(event.currentTarget.classList[0] === 'crumb'){
                if(index){
                    dispatch({type: 'new', payload: crumbsList.splice(0, index + 1)})
                }
            }
        }
    }

    return (
        <div className='bread__crumbs'>
            <nav className='bread__crumbs-list'>
                {crumbsList.length && crumbsList.map((link: string, index: number) => {
                    if(index + 1 === crumbsList.length){
                        return <span key={link}>{link}</span>
                    }
                    return <Link key={link} className='crumb' onClick={event => replace(event, link)} to={link}>{link}</Link> 
                })}
            </nav>
        </div>
    )
}

export default Crumbs
