import {ReactElement, useState} from 'react'

interface Scroll {
    children: ReactElement,
    height: string
}

export function Scroll({children, height}: Scroll) {
    
    const [hidden, setHidden] = useState(true)

    return (
        <>
            <div className="scroll">
                <div className="scroll__content" style={hidden ? {height, overflow: 'hidden'} : {height: '100%'}}>
                    {children}
                </div>
                <div className="scroll-btn" onClick={()=>setHidden(prev => !prev)}>{hidden ? '' : ''}</div>
            </div>
        </>
    )
}

export default Scroll