import { useEffect, useState } from 'react';
import { Params, useParams } from 'react-router-dom';
import ProductsService from '../../API/API';
import useProducts from '../../components/hooks/useProducts';
import Favourite from '../../shared/Favourite';
import GoBack from '../../shared/GoBack/GoBack';
import { Scroll } from '../../shared/Scroll';
import ProductType from '../../types/ProductType';
import MiniSlider from '../MiniSlider/MiniSlider';

interface parameters {
  [propName: string]: string
}

function SingleProductContent() {

    const [product, setProduct] = useState<ProductType>({
      type: '',
      img: [],
      property: {},
      props: [],
      title: '',
      description: '',
      _id: ''
    })

    const params: Params<string> = useParams()
    const id = Number(params.id)

    const [fetch] = useProducts(async ()=> {
      const response: ProductType = await ProductsService.getById(params.id)
      const RAMParams: parameters = await ProductsService.getLap()
      const keys: string[] = Object.keys(RAMParams)
      if(response.type === 'Laptops'){
        keys.map(key => {
          if(key === response.property.RAM){
            alert(RAMParams[key])
          }
          return;
        })
      }
      setProduct({...response, props: [...Object.keys(response.property)]})
    })

    useEffect(()=>{
        fetch()
    }, [])

    return (
        <div className='single__product-page '>
            <div className='single__product-title'>
              <GoBack />
            </div>
            <div className='single__product-content'>
              <div className="single__product-info">
                <h2>{product.title}</h2>
                <p>
                  {product.description}
                </p>
                <h3>: </h3>
                <Scroll height='20vh'>
                  {product.props && <div className="single__product-specifications">
                    {product.props.map((i: string) => {
                      return <div className='specification' key={i}>
                        <p>{i}: {product.property[i]}</p>
                      </div>
                    })}
                  </div>}
                </Scroll>
                <Favourite id={id}/>
              </div>
              <div className='single__product-image'>
                <MiniSlider slidesList={product.img} id={id} />
              </div>
            </div>
        </div>
    )
}

export default SingleProductContent
