
import { MouseEvent } from "react"

interface SwapBtnsI {
  value: boolean,
  inner: string,
  classNames: string,
  setValue: Function
}

function SwapBtn({setValue, value, inner, classNames}: SwapBtnsI) {

    function swapBtn(event: MouseEvent<HTMLButtonElement>){
      const btns: NodeListOf<HTMLButtonElement> = document.body.querySelectorAll('.swap-btn')
      btns.forEach(i => i.classList.remove('active-swap'))
      event.currentTarget.classList.add('active-swap')
      setValue(value)      
    }

    return (
      <button className={classNames} onClick={swapBtn}>{inner}</button>
    )
}

export default SwapBtn
