
import { useState } from 'react'
import MyForm from './ui/MyForm';
import SwapBtn from './ui/SwapBtn';
import UserService from '../../API/UserService';

function UserForm() {

    const [currentForm, setCurrentForm] = useState<boolean>(true)

    return (
        <div className='user__form'>
            <div className="swap__btns-list">
                <SwapBtn setValue={setCurrentForm} value={true} inner='signup' classNames='swap-btn active-swap' />
                <SwapBtn setValue={setCurrentForm} value={false} inner='signin' classNames='swap-btn' />
            </div>
            {currentForm
                ?<MyForm title='Create a new Account' method={UserService.reg} />
                :<MyForm title='Go in your account' method={UserService.log} />
            }
        </div>
    )
}

export default UserForm;
