
import React, { ReactChild, ReactNode } from "react";

interface IModal {
    children: ReactChild | ReactNode,
    close: () => any
}

export function Modal({children, close}: any) {

    return (
        <>
            <div className="modal-window" onClick={(event): void => {
                if((event.target as Element).classList.contains('modal-window')){
                    document.body.classList.remove('lock')
                    close()
                }
            }}>
                <div className="modal-content">{children}</div>
            </div>
        </>
    )
}