import { Link } from "react-router-dom"
import { ProductImage } from "../../../shared/ProductImage"

interface CategpriesI {
    to: string,
    category: string,
    img: string
}

export function Categories() {
    
    const categories: CategpriesI[] = [
        {to: '/search/Laptops', category: '', img: 'https://laptop.ru/upload/iblock/d81/d8119ad17242cae64a0887342748a48d.jpg'},
        {to: '/search/Phones', category: '', img: 'https://phonesdata.com/files/models/Infinix-Hot-30-Play-440.jpg'},
        {to: 'products/categories/Keyboards', category: '', img: 'https://avatars.mds.yandex.net/i?id=7ffbee614bb3af17037534ba147d136bd84b56ea-9848498-images-thumbs&n=13'},
        {to: 'products/categories/Monitors', category: '', img: 'https://nevateka.ru/upload/iblock/426/ccxhkm23d8ocgcutuupdlvhzoc7mztpw/1560514_v03_b.jpg'},
    ]

    return (
        <>
            <div className="categories">
                <h2 className="main__page-title">
                     
                </h2>
                <div className="categories__list">
                    {categories.map((c: CategpriesI) => 
                        <div className="category">
                                <div className="category__img-container">
                                    <Link to={c.to}>
                                        <ProductImage image={c.img} />
                                    </Link>
                                </div>
                                <h2><Link to={c.to}>{c.category}</Link></h2>
                        </div>
                    )}               
                </div>
            </div>
        </>
    )
}
