import React, { useMemo, useRef, useState } from 'react'
import Product from '../../enties/Product/Product'
import IProduct from '../../interfaces/IProduct'

function Slider({listProducts, slidesToShow}: {listProducts: IProduct[], slidesToShow: number}) {

    const sliderList: any = useRef()
    const sliderItem: any = useRef()
    const sliderDraggable: any = useRef()
    const [sliderPosition, setSliderPosition] = useState(0)

    let memoPosition = useMemo(()=>{
        return sliderPosition
    }, [sliderPosition])

    function next(){
        if(sliderPosition <= sliderList.current.scrollWidth){
            if(sliderList.current.scrollWidth - (sliderPosition + sliderItem.current.offsetWidth * slidesToShow) < sliderList.current.clientWidth) {
                setSliderPosition(sliderList.current.scrollWidth - sliderList.current.clientWidth)
            } else {
                setSliderPosition(prev => prev + sliderItem.current.offsetWidth * slidesToShow)
            }
        }
    }

    function prev(){
        if(sliderPosition !== 0){
            if(sliderPosition < sliderItem.current.offsetWidth * slidesToShow) {
                setSliderPosition(0)
            } else {
                setSliderPosition(prev => prev - sliderItem.current.offsetWidth * slidesToShow)
            }
        }
    }

    const Products = useMemo(()=>{
        return listProducts.map((product: IProduct) => {
            product.keys = Object.keys(product.property)
            return <Product key={Math.random()} className='slider__item' ref={sliderItem} props={product}/>
        }) 
    }, [listProducts])

    return (
        <div ref={sliderDraggable} className='slider-draggable'>
            <div className="slider__wrapper">
                <div ref={sliderList} className="slider__list" style={{transform: `translate(-${memoPosition}px, 0)`}}>
                    {Products}
                </div>
            </div>
            <button className='previous slider__button' onClick={prev} ><img src="/images/arrow.png" alt="#" /></button>
            <button className='next slider__button' onClick={next} ><img src="/images/arrow.png" alt="#" /></button>
        </div>
    )
}

export default React.memo(Slider) 
