
import { LegacyRef, BaseSyntheticEvent, forwardRef } from "react";
import { Link, NavigateFunction, useNavigate } from "react-router-dom";
import Favourite from "../../shared/Favourite";
import { AddToCompare } from "../../shared/addToCompare";
import IProduct from "../../interfaces/IProduct";
import { ProductImage } from "../../shared/ProductImage";

interface ProductModified {
    props: IProduct,
    className: string
}

const Product = forwardRef(({props, className}: ProductModified, ref: LegacyRef<HTMLDivElement>) => {

    const navigation: NavigateFunction = useNavigate()
    const id: number = Number(props['_id'])
    const ProductEvent = async (event: BaseSyntheticEvent<MouseEvent>) => {
        if(event.target.closest('.show-info')){
            navigation(`/products/${id}`)
        }
    }

    return (
        <div className={className} ref={ref} onClick={ProductEvent}>
            <div className="product">
                <div className="product-img">
                    <div className="image-container" ><Link to={`/products/${id}`}><ProductImage image={props.img[0]} /></Link></div> 
                    <div className="product__info-btns">
                        <Favourite id={id} />
                        <AddToCompare id={id} type={props.type} />                  
                    </div>
                </div>
                <div className="product__info">
                    <Link to={`/products/${id}`}>{props.title}</Link>
                </div>
            </div>
        </div>
    )    
})

export default Product;