
import { useEffect, useState } from 'react'
import FavouriteProduct from '../../enties/FavouriteProduct/FavouriteProduct';
import Crumbs from '../../enties/Crumbs/Crumbs';
import { useSelector } from 'react-redux';
import useProducts from '../../components/hooks/useProducts'
import Store from '../../types/Store';
import IProduct from '../../interfaces/IProduct';
import API from '../../API/API'

function Favourites() {

    const [products, setProducts] = useState<IProduct[]>([])
    const favourites: number[] = useSelector((state: Store) => state.favourites.list.list)
    
    const [fetch, isLoad] = useProducts(async ()=> {
      const response: IProduct[] = await API.getFavourites(favourites)
      console.log(response)
      setProducts(response)
    })

    useEffect(()=>{
      if(favourites) {
        fetch()
      }
    }, [favourites])

    return (
      <div className='favourites'>
        <Crumbs />
        <div className="favourites__products">
          <h1>Favorites</h1>
          {products.length === 0 && <h1>{',            :('}</h1> }
          {isLoad && products.map(product => {
            return <FavouriteProduct key={product._id + product.description} className='favourite__product' props={product} />
          })}
        </div>
      </div>
    )
}

export default Favourites;
