import { BaseSyntheticEvent, useState } from 'react'
import { SearchList } from './searchList'
import { Link } from 'react-router-dom'

export function SearchInput() {
    
    const [searchQuery, setSearchQuery] = useState<string>('')
    const [isList, setIsList] = useState<boolean>(false)

    const changeValue = (event: BaseSyntheticEvent) => {
        setSearchQuery(event.target.value)
    }

    document.body.addEventListener('click', (event: any) => {
        if(!event.target.closest('.search-list') && !event.target.closest('.search-input')){
            setIsList(false)
        }
    })

    return (
        <>
            <div className="search-input">
                <input
                    placeholder=''
                    type="text" value={searchQuery}
                    onChange={changeValue}
                    onFocus={() => setIsList(true)}
                />
                {(searchQuery.length > 0 && isList) && <SearchList search={searchQuery.toLowerCase()} />}
                <div className="search-btn"><Link to={`/search/${searchQuery}`}><img src="/images/black-search.png" alt="" /></Link></div>
            </div>
        </>
    )
}
