import React from 'react'
import IProduct from '../../../interfaces/IProduct'
import getColor from '../hooks/getColor'

interface CompareProductI {
    // product: IProduct,
    // keys: string[],
    totalData: {[propName: string]: number}[],
    products: any
    // index: number,
}

interface specList {
    values: {
        [propName: string]: string[]
    },
    colors: {
        [propName: string]: string[]
    }
}

export function CompareProducts({totalData, products}: CompareProductI) {
    
    let specificationsList: specList = {values: {}, colors: {}}
    const thisData: {[propName: string]: number}[] = totalData
    const totalDataKeys = Object.keys(thisData[0])

    totalDataKeys.forEach((key: string) => {
        let values: string[] = []
        let colors: string[] = []
        totalData.forEach((data: {[propName: string]: number}, index: number) => {
            values.push(products[index].property[key])
            colors.push(getColor(data[key]))
        })
        specificationsList.values[key] = values
        specificationsList.colors[key] = colors
    })
    
    console.log(specificationsList)

    return (
        <>
            <div className="products__titles">
                {products.map((product: IProduct) => {
                    return <h1 className='product__title'>{product.title}</h1>
                })}
            </div>
            {totalDataKeys.map((key: string, index: number) => {
                return <>
                    <div className="specifications__list">
                        <div className="product__specifications">
                            <h1 className='specification-title'>{key}</h1>
                            <div className="product__specifications-specifications">
                                {specificationsList.values[key].map((value: string, innerIndex: number) => {
                                    let specClass = !innerIndex ? 'product__specification first-product' : 'product__specification second-product'
                                    return <h1 className={specClass} style={{animationDelay: `${index/6}s`, backgroundColor: `rgba(${specificationsList.colors[key][innerIndex]}, 1)`}}>{value}</h1>
                                })}
                            </div>
                        </div>
                    </div>

                </>
            })}
        </>
    )
}
