

type defaultFavT = {
  list: Array<number>
}

const defaultState: defaultFavT = {
    list: []
}

type favT = {
  type: string,
  payload: Array<number>
}

export const favourites = (state = defaultState, action: favT) => {
    switch(action.type){
      case 'addFavourite':
        return {...state, list: action.payload}

      default: 
        return state
    }
}