import { useEffect, useState, useMemo } from 'react'
import { useSelector } from 'react-redux'
import { CompareProduct } from '../ui/CompareProduct'
import { CompareColors } from '../ui/compareColors'
import useProducts from '../../../components/hooks/useProducts'
import API from '../../../API/API'
import Store from '../../../types/Store'
import IProduct from '../../../interfaces/IProduct'
import compareAPI from '../CompareAPI/compareAPI'
import PhonesI from '../../../interfaces/PhonesI'
import LaptopsI from '../../../interfaces/LaptopsI'
import useGetTotalData from '../hooks/useGetTotalData'
import { CompareProducts } from '../ui/CompareProducts'

export function CompareZone() {

    const compareList: number[] = useSelector((state: Store) => state.compare.compareIdies)
    const compareTypes: string[] = useSelector((state: Store) => state.compare.compareTypes)
    const [keys, setKeys] = useState<Array<string>>([])
    const [compareProducts, setCompareProducts] = useState<IProduct[]>([])
    const [compareData, setCompareData] = useState<PhonesI | LaptopsI>({})
    const getTotalData = useGetTotalData()
    
    const [fetchCompareProducts, load] = useProducts(async ()=> {
        const response: IProduct[] = await API.getFavourites(compareList)
        let keysList: string[][] = []
        response.map((product: IProduct) => {
            keysList.push(Object.keys(product.property))
        })
        console.log(keysList)
        if(keysList[0].length > keysList[1].length){
            setKeys(keysList[0])
        } else {
            setKeys(keysList[1])
        }
        setCompareProducts(response)
    })

    const [fetchCompareData, isLoad] = useProducts(async () => {
        let data = {}
        if(compareTypes[0] === 'Phones'){
            data = await compareAPI.getPhonesData()
        } else if(compareTypes[0] === 'Laptops') {
            data = await compareAPI.getLaptopsData()
        }
        setCompareData(data)
    })

    useEffect(()=>{
        fetchCompareProducts()
        fetchCompareData()
    }, [compareList])

    const totalData = compareProducts.map((product: IProduct) => {
        return getTotalData(keys, compareData, product)
    })

    const totalProducts = useMemo(() => {
        return compareProducts.map((product: IProduct, index: number) => {
            return <CompareProduct 
                    key={product._id}
                    product={product}
                    index={index}
                    keys={keys}
                    totalData={totalData}
                    />
        })
    }, [compareData])

    return (
        <div className="compare__zone content__page-content">
            <h1>COMPARE</h1>
            <CompareColors />
            <div className="compare__products">
                {load && isLoad && compareProducts.length > 0 && <CompareProducts products={compareProducts} totalData={totalData} />}
                {/* {totalProducts.map((product: any) => {
                    return product
                })} */}
            </div>
        </div>
    )
}
