
interface TitlesI {
    list: string[]
}

interface ActionI {
    type: string,
    payload: string[]
}

const defaultState: TitlesI = {
    list: []
}

export const titles = (state = defaultState, action: ActionI) => {
    switch (action.type) {
        case 'changeTitles':
            return {...state, list: action.payload}

        default:
            return {...state}
    }
}

export default titles