
export default function(rating: number) {

    return rating >= 0 && rating <= 1000 ? '85, 22, 90' :
    rating > 1000 && rating <= 2000 ? '251, 101, 251' :
        rating > 2000 && rating <= 3000 ? '62, 117, 254' :
            rating > 3000 && rating <= 4000 ? '1, 228, 254' :
                rating > 4000 && rating <= 5000 ? '118, 250, 41' :
                    rating > 5000 && rating <= 6000 ? '254, 113, 57' :
                        rating > 6000 && rating <= 7000 ? '255, 49, 46' : ''
}