
import { useEffect, useState } from "react";
import { Modal } from "../../shared/Modal";
import Catalog from "./ui/Catalog";
import UserForm from "../../features/UserForm/UserForm";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import addToNav from "./addToNav";
import Store from "../../types/Store";
import { Dispatch } from "redux";
import API from '../../API/API'
import useProducts from "../../components/hooks/useProducts";
import { SearchInput } from "./ui/SearchInput/SearchInput";
import searchResponseI from "../../interfaces/responseProductI";
import IProduct from "../../interfaces/IProduct";

export function Header() {

    const [modal, setModal] = useState<boolean>(false)
    const [adaptive, setAdaptive] = useState<boolean>(false)
    const isAuth: boolean = useSelector((state: Store) => state.isAuth.auth)
    const dispatch: Dispatch = useDispatch()
    const crumbsList: string[] = useSelector((state: Store) => state.crumbs.list)
    let userClass = isAuth ? 'user adaptive' : 'user'
    const [fetch, isLoad, error] = useProducts(async () => {
        const data: searchResponseI = await API.filterProducts({search: '', type: 'none', filters: []})
        let list: string[] = []
        data.products.forEach((i: IProduct) => {
            list.push(i.title.toLowerCase())
        })
        dispatch({type: 'changeTitles', payload: list})
    })

    useEffect(()=>{
        if(document.body.clientWidth < 700){
            setAdaptive(true)
        }
        fetch()
    }, [])

    return (
        <header className="wrapper">
            <Catalog adaptive={adaptive} />
            <SearchInput />
            <div className={userClass} onClick={()=>{
                    if(!isAuth){
                        setModal(true)
                        document.body.classList.add('lock')
                    }
                }}>
                <Link to={isAuth ? '/cabinet' : ''} onClick={event => addToNav(event, crumbsList, dispatch)}>
                    {isAuth && <img src="/images/white-new-user.png" alt="#" />}
                    {!adaptive && !isAuth && <p>Reqistered or login</p>}
                    {adaptive && !isAuth && <img src="/images/white-new-user.png" alt="#" />}
                </Link>
            </div>
            {modal && <Modal close={setModal} >
                <UserForm />
            </Modal>}
        </header>
    )
}