import IProduct from "../../../interfaces/IProduct"


interface CompareProductI {
    product: IProduct,
    keys: string[],
    totalData: {[propName: string]: number}[],
    index: number,
}

export function CompareProduct({product, keys, totalData, index}: CompareProductI) {

    let thisData: {[propName: string]: number} = totalData[index]
    const classNameList = index ? 'product__specification second-product' : 'product__specification first-product'
    const totalDataKeys = Object.keys(thisData)
    const result: {[propName: string]: string} = {}

    totalDataKeys.forEach((key: string) => {
        
        let td = thisData[key]
        return td >= 0 && td <= 1000 ? result[key] = '85, 22, 90' :
            td > 1000 && td <= 2000 ? result[key] = '251, 101, 251' :
                td > 2000 && td <= 3000 ? result[key] = '62, 117, 254' :
                    td > 3000 && td <= 4000 ? result[key] = '1, 228, 254' :
                        td > 4000 && td <= 5000 ? result[key] = '118, 250, 41' :
                            td > 5000 && td <= 6000 ? result[key] = '254, 113, 57' :
                                td > 6000 && td <= 7000 ? result[key] = '255, 49, 46' : ''
    })

    let resultKeys: string[] = [...totalDataKeys]
    const isBest: string[] = []
    resultKeys.forEach((key: string) => {
        if(index == 1){
            if(thisData[key] > totalData[0][key]){
                isBest.push('<-')
            } else if(thisData[key] < totalData[0][key]){
                isBest.push('->')
            } else {
                isBest.push('=')
            }
        }
    })

    keys.forEach((key: string) => {
        if(!resultKeys.includes(key)){
            resultKeys.push(key)
        }
    })
    console.log(resultKeys)

    console.log(Object.keys(product.property))

    return (
        <>
            <div className="compare__product">
                <div className="product-title">{product.title}</div>
                <ul className="product__specifications">
                    {resultKeys.map((key: string, i: number) => {
                      return <>
                            {!index && <p>{key}</p>}
                            <li
                              className={classNameList}
                              style={{animationDelay: `${i/6}s`, backgroundColor: result[key] && `rgba(${result[key]}, 1)`}}
                              key={key + product._id} onLoad={(event) => console.log(event)}
                            >
                        
                            <p>{product.property[key]}</p>
                            <p className="product__specification-sign">{isBest[index]}</p>
                            </li>
                      </>
                    })}
                </ul>
            </div>
        </>
    )
}

export default CompareProduct