
enum Specifications {
    RAM = ' ',
    CPUSeries = ' .',
    TypeOfRAM = '  ',
    NumberOfCPUCores = '  ',
    DisplayDiagonal = ' ',
    ScreenMatrixType = '  ',
    Webcam = ' ',
    CPUFrequency = ' ',
    DisplayResolution = ' ',
    CPU = '',
    TypeOfDataCarrier = '  ',
    TypeOfGPU = ' ',
    SSDSolidStateDriveCapacity = '   SSD',
    CPUFrequencyTurbo = '  ( Turbo)',
    SoundSystem = '',
    ANTUTU9 = 'Antutu 9 ',
    BATTERY = '',
    DISPLAY = "",
    DPI = "  ",
    GEEKBENCH_MC = "GeekBench 5 Multi-Core",
    GEEKBENCH_SC = "GeekBench 5 Single Core",
    GPU = " ",
    INTERNAL_STORAGE = " ",
    SP = " ",
    TECHNOLOGIES = ""

}

export default function(){
    
    return function(keys: string[], compareData: any, product: any){
        const totalData: {[propName: string]: number} = {}
        console.log(keys, product, compareData)
        keys.forEach((key: string) => {

            let compareDataKeys = Object.keys(compareData)
            let spec = product.property[key]
                
            compareDataKeys.forEach((compareKey: string) => {
                if(Specifications[compareKey as keyof typeof Specifications] === key){
                    console.log(compareData[compareKey])
                    let compareValue = compareData[compareKey][spec]
                    if(compareValue !== undefined){
                        console.log(1)
                        totalData[key] = compareValue
                    }
                }
            })
        })

        return totalData    
    }
}