
import IProduct from '../../interfaces/IProduct'

function Description({props}: {props: IProduct}) {
    return (
        <div className='description'>
            <h2 className="description__title">{props.title}</h2>
            <h2 className="description__price">{props._id}{props._id}{props._id}{props._id}</h2>
            <h5 className="date">{props._id}.{props._id}.{props._id}{props._id}</h5>
            <h3>Description:</h3>
            <p className='description__info'>{props.description}</p>
        </div>
    )
}

export default Description
