import React, { LegacyRef, forwardRef } from "react";
import { useNavigate } from "react-router-dom";
import Description from "./Description";
import { useDispatch } from "react-redux";
import { Dispatch } from "redux";
import IProduct from "../../interfaces/IProduct";
import MiniSlider from "../../features/MiniSlider/MiniSlider";
import { AddToCompare } from "../../shared/addToCompare";
import { RemoveFav } from "../../shared/RemoveFav";
import ShowInfo from "../../shared/ShowInfo";

type FavProductT = {
    props: IProduct,
    className: string
}

const FavouriteProduct = forwardRef(({props, className}: FavProductT, ref: LegacyRef<HTMLDivElement>) => {

    const navigation = useNavigate()
    const id: number = Number(props._id)
    props._id = id
    const dispatch: Dispatch = useDispatch()

    const ProductEvent = async (event: React.BaseSyntheticEvent<MouseEvent>) => {
        if(event.target.classList[0] === 'show-info'){
            navigation(`/products/${id}`)
        }
    }

    return (
        <>
            <div className={className} ref={ref} onClick={ProductEvent}>
                <div className="favourite__img">
                    <MiniSlider id={id} slidesList={props.img}></MiniSlider>
                    <div className="product__info-btns">
                        <AddToCompare id={props._id} type={props.type} />
                        <ShowInfo />
                        <RemoveFav id={props._id} />
                    </div>
                </div>
                <Description props={props} />
            </div>
        </>
    )    
})

export default FavouriteProduct;