import IProduct from "../interfaces/IProduct"

type defaultT = {
    compareTypes: string[],
    compareIdies: number[]
}

const defaultState: defaultT = {
    compareTypes: [],
    compareIdies: []
}

type obj = {
    payload: {
        idies: number[],
        types: string[]
    },
    type: string
}

export const compare = (state = defaultState, action: obj) => {

    switch (action.type) {

        case 'changeCompareList':
            return {
                ...state,
                compareIdies: action.payload.idies,
                compareTypes: action.payload.types
            }
            
        default:
            return state
    }
}