

type defaultType = {
  auth: boolean,
  userData: {
    login: string,
    favourites: number[]
  }
}

const defaultState: defaultType = {
    auth: false,
    userData: {
      login: '',
      favourites: [],
    }
}

type isAuthT = {
  type: string,
  payload: defaultType
}

export const isAuth = (state = defaultState, action: isAuthT) => {
    switch(action.type){
      case 'AUTH_TRUE':
        return {...state, auth: action.payload.auth, userData: action.payload.userData}
        
      default: 
        return state
    }
}