
import {useEffect} from 'react'
import { Route , Routes, Navigate} from 'react-router-dom'
import { privateRoutes, publicRoutes } from './routes';
import { useSelector, useDispatch } from 'react-redux';
import UserService from '../API/UserService';
import Store from '../types/Store';

function AppRouter() {

    const isAuth: boolean = useSelector((state: Store) => state.isAuth.auth)
    const dispatch = useDispatch()
    
    async function getData(){

      let data;

      try {
        data = await UserService.autorization()
      } catch (error) {
        console.log(error)
      }
      
      if(data !== undefined){
        dispatch({type: 'AUTH_TRUE', payload: {auth: true, userData: data.userData}})
        dispatch({type: 'addFavourite', payload: {list: data.userData.favourites}})
      } else {
        let list = sessionStorage.getItem('favouritesList')
        let favourites = list ? JSON.parse(list).list : []
        dispatch({type: 'addFavourite', payload: {list: favourites}})
      }
    }

    useEffect(() => {
      getData()
    }, [])

    return (
      isAuth 
      ?
      <Routes>
        {privateRoutes.map(route => 
          <Route path={route.path} element={route.element} key={route.key} />
        )}
        {/* <Route path='/' element={<Navigate to='/Main' replace />} /> */}
      </Routes>
      :
      <Routes>
        {publicRoutes.map(route => 
          <Route path={route.path} element={route.element} key={route.key} />
        )}
      </Routes>
    )
}

export default AppRouter
