import { useDispatch, useSelector } from 'react-redux'
import Store from '../types/Store'

interface addToCompareI {
    id: number,
    type: string
}

export function AddToCompare({id, type}: addToCompareI) {
    
    const compareIdies = useSelector((state: Store) => state.compare.compareIdies)
    const compareTypes = useSelector((state: Store) => state.compare.compareTypes)
    const dispatch = useDispatch()

    function changeCompareList(){
        if(compareTypes.length && compareTypes.every(compareType => compareType === type)){
            if(!compareIdies.includes(id) && compareIdies.length < 3){
                compareIdies.push(id)
                compareTypes.push(type)
                dispatch({type: 'changeCompareList', payload: {
                    idies: compareIdies,
                    types: compareTypes
                }})
            } else if(!compareIdies.includes(id)) {
                compareIdies[1] = id
                compareTypes[1] = type
                dispatch({type: 'changeCompareList', payload: {
                    idies: compareIdies,
                    types: compareTypes
                }})
            }
        } else if(compareTypes.length){
            let isComfirmed = window.confirm('   ,         ?')
            if(isComfirmed){
                dispatch({type: 'changeCompareList', payload: {
                    idies: [id],
                    types: [type]
                }})  
            }
        } else {
            compareIdies.push(id)
            compareTypes.push(type)
            dispatch({type: 'changeCompareList', payload: {
                idies: compareIdies,
                types: compareTypes
            }})     
        }
    }
    return (
        <>
            <div className="add-to-compare" onClick={changeCompareList}>
                <img src="/images/compare.svg" alt="  " />
            </div>
        </>
    )
}
