
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from "react-redux";
import Store from "../types/Store";
import favourite from '../helpers/addFavourite';
import { Dispatch } from 'redux';

function Favourite({id}: {id: number}) {

    let favList = useSelector((state: Store) => state.favourites.list.list)
    const isAuth = useSelector((state: Store) => state.isAuth.auth)
    const [isFavourite, setIsFavourite] = useState(false)
    const dispatch: Dispatch = useDispatch()

    useEffect(()=>{
        if(favList){
            if(favList.includes(id)){
                setIsFavourite(true)
            } 
        }
    }, [favList])

    const favEvent = async () => {
        setIsFavourite(prev => !prev)
        if(isAuth){
            await favourite(id, dispatch)
        } else {
            let list = sessionStorage.getItem('favouritesList')
            if(!list){
                sessionStorage.setItem('favouritesList', JSON.stringify({list: [id]}))
                favList = [id]
            } else {
                let favourites = JSON.parse(list).list
                if(!favourites.includes(id)){
                    favourites.push(id)
                } else {
                    let index = favourites.indexOf(id)
                    favourites.splice(index, 1)
                }
                sessionStorage.setItem('favouritesList', JSON.stringify({list: favourites}))
                favList = favourites
                dispatch({type: 'addFavourite', payload: {list: favList}})
            }
        }
    }

    return (
        <div  className="favourite" title='  ' onClick={favEvent}>
            <img src={isFavourite ? '/images/red-heart-border.png' : '/images/heart.png'} alt="#" />
        </div>
    )
}

export default Favourite;
