import {useState, useEffect, useMemo, useRef} from "react"
import ProductsService from "../../API/API";
import useProducts from "../../components/hooks/useProducts";
// import ProductPagination from "./UI/Pagination";
import Slider from "../../features/Slider/Slider";
import useGetSlidesCount from './helpers/useGetSlidesCount'
import Loader from "../../components/UI/Loader";
import { useDispatch } from "react-redux";
import IProduct from "../../interfaces/IProduct";

export function ListProducts(){

    const [products, setProducts] = useState<Array<IProduct[]>>([])
    const [limit] = useState<number>(10)
    const [totalPages, setTotalPages] = useState<number>(0)
    const [page, setPage] = useState<number>(1)
    const slides = useGetSlidesCount()
    const dispatch = useDispatch()
    // let lastProduct = useRef()
    // let observer: any = useRef()
    let slidersList = []

    const [fetchProducts, load] = useProducts(async ()=> {
      const responseProducts = await ProductsService.getAll(limit, page)
      setProducts([...products, responseProducts.data])
      let total = responseProducts.headers['x-total-count']
      if(totalPages === 0){
        setTotalPages(Math.ceil(total / limit))
      }
    })

    useEffect(()=>{

      fetchProducts()

      dispatch({type: 'new', payload: ['/Main']})

      // if(load) return;

      // if(observer.current) observer.current.disconnect();

      // let callback = function(entries: any){
      //     if(entries[0].isIntersecting && limit <= products.length){
      //       fetchProducts()
      //     }
      // };

      // observer.current = new IntersectionObserver(callback);
      // observer.current.observe(lastProduct.current);

      // if(observer.current) observer.current.disconnect();

    },[page])

    slidersList = useMemo(() => {
      return products.map((listProducts: IProduct[]) => {
        return <Slider key={Math.random()} listProducts={listProducts} slidesToShow={slides}/>})
    }, [products])
    
    return(
      <>
        <div className="content">
          <div className="container">
            {slidersList}
          </div>
          {!load && <Loader />}
          {/* <ProductPagination ref={lastProduct} onClick={() => 
                {if(page < totalPages) setPage((prev:number) => prev += 1)}}  /> */}
        </div>
      </>
    )
}