import { useState } from 'react'
import MyForm from '../features/UserForm/ui/MyForm'
import UserService from '../API/UserService'
import { Modal } from './Modal'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import Store from '../types/Store'

function User() {

    const [modal, setModal] = useState<boolean>(false)
    const {login, favourites} = useSelector((state: Store) => state.isAuth.userData)

    return (
      <div className="user__page content__page-content">
        <div className="user__page-cabinet">
          <div className="user__page-card">
            <div className="user__header">
              <div className="user__page-avatar"><img src="../../images/.jpeg" alt="" /></div>
              <ul className="user__menu">
                <li className="user__menu-button">
                  <Link to='/favourites'>favourites</Link>
                </li>
                <li className="user__menu-button">
                  <Link to='/products'>Products</Link>
                </li>
                <li className="user__menu-button">
                  <Link to='/'>Main</Link>
                </li>
                <li className="user__menu-button">
                  <Link to='/'>Compare</Link>
                </li>
              </ul>
            </div>
            <div className="user__data">
              <p>{login}</p>
              {/* <p>{favourites.join(' ')}</p> */}
            </div>
          </div>
          {modal && <Modal close={setModal}>
            <MyForm title='Recovery' recovery={true} method={UserService.postRecoveryData} />
          </Modal>}
          <div className="user__functions">
            <div className="recovery-btn user-btn" onClick={()=>{
              setModal(true)
            }}>Change password...</div>
            <div className="log-out-btn user-btn" onClick={async ()=>{
              if(window.confirm()){
                sessionStorage.clear()
                UserService.logOut()
              }
            }}>LogOut...</div>
          </div>
        </div>
      </div>
    )
}

export default User
