
import { useState, useEffect } from 'react'

type ProductStyle = {
    width?: string | undefined,
    height?: string | undefined
}

export function ProductImage({image}: {image: string | undefined}) {

    const isAdaptive = document.body.offsetWidth < 700 ? true : false

    const imgLoad = (event: any) => {
        if(image !== undefined){
            const width = event.target.width
            const height = event.target.height
            if(width > height){
                event.target.style.width = 'auto';
                event.target.style.maxHeight = isAdaptive ? 'auto' : '15vw';
            } else {
                event.target.style.height = 'auto';
                event.target.style.maxWidth = isAdaptive ? 'auto' : '15vw';      
            }
        }
    }

    return (
        <>
            <img className='adaptive-img' onLoad={imgLoad} src={image} alt="" />
        </>
    )
}
